import React, {useEffect,useState} from 'react';
import {
Typography,
Avatar,
Grid,
Box,
IconButton,
Divider,
Paper
} from "@material-ui/core";
/**import avatar from "../images/profilepic.png"; **/
import Typed from "react-typed";
import {makeStyles} from "@material-ui/core/styles";
import {motion} from "framer-motion";
import {Link} from "react-scroll";
import arrow from "../images/arrow.png";
import background from "../images/background.jpg";
import Particles from "react-particles-js";
import concordiaImg from "../images/concordia-university.jpg"
import Contacts from "./Contacts";
import About from "./About";
import "../styles/logo.scss";

// CSS STYLES
const useStyles = makeStyles(theme=> ({
    title: {
        position: "absolute",
        transform: "translate(-50%, -50%)", 
        top: "35%",
        left: "50%",
        color: "black",
        fontWeight: "500",
        fontSize: "8vw",
        zIndex: -1
    },
    container: {
        position: "relative",
        width: "100%",
        height: "100vh",
        textAlign: "center",
        background: "url("+ background +")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "contain",
        backgroundSize: "cover",
        backgroundPosition: "center",
        zIndex: 1
    },
    particlesCanvas: {
        position: "absolute",
        opacity: "0.9",
        right: "0px"     
    },
}));

const Home = () => {
    const classes = useStyles();
    return (
        <>
            <IconButton 
                component={Link} to="about" smooth={true} duration={1000} offset={-63} style={{position: "absolute", top:"90%", left:"50%", transform: "translateX(-50%)"}}
            >
             <img src={arrow} style={{width: '35px', height: '35px'}}></img> 
            </IconButton>
      
        <div class="container">
            <div class="box">
                <div class="title">
                    <span class="block"></span>
                    <h1>Sacha Elkaim<span></span></h1>
                </div>
                <div class="role">
                    <div class="block"></div>
                    <p>Software Engineer</p>
                </div>
            </div>
       
        </div>
        <div id="about">
          <About/>
        </div>
        <div id="contact">
          <Contacts/>
        </div>
        </>
    )
}

export default Home
