import React, {useEffect} from 'react'
import { makeStyles } from '@material-ui/core/styles';
import {
Grid,
TextField,
Box,
Typography,
Button
} from "@material-ui/core";
import Aos from "aos";
import "aos/dist/aos.css";

const useStyles = makeStyles((theme) => ({
    root: {
      '& .MuiTextField-root': {
         width: 300
      },
    },
    form: {
        
        
      
    }
  }));

const Contacts = () => {
    const classes = useStyles();

    useEffect(() => {
        Aos.init({ 
            offset: 120,
            duration: 500,
            delay: 0
        });
    }, []);

    return (
       <>
       <Box component="div" className={classes.root} style={{height: "500px", marginTop: "400px"}}>
           <Grid container justify="center">
      <Box component="form" className={classes.form} noValidate autoComplete="off" style={{ padding:"20px", overflowX: "hidden"}}>
          <Typography variant="h5" style={{textAlign:"center"}}> Contact Me</Typography>
      <TextField 
      data-aos="fade-right"
      id="outlined-basic" 
      label="Name" 
      variant="outlined" 
      margin="dense"
      />
      <br/>
      <TextField 
      data-aos="fade-left"
      id="outlined-basic" 
      label="Email" 
      variant="outlined"
      margin="dense" 
      />
      <br/>
      <TextField 
      data-aos="fade-right"
      id="outlined-basic" 
      label="Message" 
      variant="outlined"  
      margin="dense"
      multiline rows={5} 
      />
      <br/>
      <Button 
      variant="outlined" 
      fullWidth={true}
      data-aos="fade-left"
      >Submit</Button>
      </Box>
      </Grid>
      </Box>

     
       </>
    )
}

export default Contacts
