import React, {useState} from 'react'
import {
AppBar,
Toolbar,
ListItem,
IconButton,
ListItemText,
ListItemIcon,
Divider,
List,
Typography,
Box
} from "@material-ui/core";
import {
Info,
Home,
FitnessCenter,
AccountTree,
ContactMail
} from "@material-ui/icons";
import {motion} from "framer-motion";
import {makeStyles} from "@material-ui/core/styles";
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import {Link as LinkScroll} from "react-scroll";

const svgVariants = {
    hidden: {rotate: -180},
    visible: {
      rotate: 0,
      transition: {duration : 1}
    }
  }
  
  const pathVariants = {
    hidden: {
      opacity: 0,
      pathLength: 0
    },
    visible: {
      opacity: 1,
      pathLength: 1,
      transition: {
        duration: 2,
        ease: "easeInOut",   
      }
    }
  }

// CSS STYLE
const useStyles= makeStyles(theme=> ({
  menuSliderContainer: {
    width: 220,
    background: "white", 
    opacity: "100%",
    height: "100%"
  },
  menuCategories: {
    fontSize:"0.9em",
    color: "black",
    fontWeight: "500"
  },
  menuTitle: {
    fontSize: "1.3em",
    paddingTop: "5%",
    paddingLeft: "8%"
  },
  version: {
    fontSize: "0.7em",
    paddingBottom: "1%",
    paddingLeft: "8%"
  }

}));

const menuItems = [
  {
    listIcon: <Info/>,
    listText: "About",
    listPath: "about"
  },
  {
    listIcon: <FitnessCenter/>,
    listText: "Experience",
    listPath: "experience"
  },
  {
    listIcon: <AccountTree/>,
    listText: "Projects",
    listPath: "projects"
  },
  {
    listIcon: <ContactMail/>,
    listText: "Contact",
    listPath: "contact"
  }
]

const Navbar = () => {
    const classes = useStyles();
    const [state,setState] = useState({
      true: false
    })

    const toggleSlider = (slider, open) => () => {
      setState({ ...state, [slider]: open});
    }

    const sliderMenu = slider => (
       <Box className={classes.menuSliderContainer}  component="div">
         <ListItemText primary="Sacha Elkaim" classes={{primary:classes.menuTitle}}/>
         <ListItemText primary="v1.0.1" classes={{primary:classes.version}}/>
         <Divider/>
         <List>
           {menuItems.map((lstItem, key) => (
            <ListItem button key={key} component={LinkScroll} to={lstItem.listPath} smooth={true} duration={500} offset={-63} onClick={toggleSlider(slider, false)}>
              
              <ListItemText classes={{primary:classes.menuCategories}} primary={lstItem.listText}/>
            </ListItem>
           ))}
           <Divider/>
         </List>
       </Box>
    )
    return (
        <>
        <Box component="nav">
            <AppBar 
              position="fixed" 
              style={{background: "#121416", opacity:"85%"}}>
                <Toolbar>
                    <IconButton onClick={toggleSlider("true", true)}>
                      <motion.svg 
                        height="40" 
                        width="40"  
                        variants={svgVariants}
                        initial='hidden'
                        animate='visible'>
                        <g fill="none">
                          <motion.path stroke="white" strokeWidth="2"  d="M5 10 l30 0" variants={pathVariants}/>
                          <motion.path stroke="white" strokeWidth="2"  d="M5 20 l30 0" variants={pathVariants}/>
                          <motion.path stroke="white" strokeWidth="2"  d="M5 30 l30 0" variants={pathVariants}/>
                        </g>
                      </motion.svg>
                    </IconButton>
                    <Typography 
                      style={{color: "white", fontSize: "19px"}}
                    >
                         Portfolio
                    </Typography>
                    <SwipeableDrawer 
                      anchor="left"
                      open={state.true}
                      onClose={toggleSlider("true", false)}
                    >
                       {sliderMenu("true")}
                    </SwipeableDrawer>
                </Toolbar>
            </AppBar>
        </Box>
      </>
    )
}

export default Navbar
